@import url("../../../assets/feather/style.min.css");
@import url("../../../assets/fontawesome/css/all.css");
@import url("../../../assets/line-awesome/css/line-awesome.min.css");
:host {
  display: flex;
  text-align: left;
  align-items: center;
  flex-direction: column;
}
.date-range-input {
  border: 0;
  outline: 0;
  margin: 0;
  flex: 1;
}
.date-range-input:focus {
  border: 0;
  outline: 0;
}
.iglRangePicker {
  margin: 0;
  border: 1px solid #379ff2;
  border-radius: 6px;
  min-width: 300px;
  max-width: 320px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  text-align: center;
  margin-right: 5px;
  padding: 5px 2rem;
}
.calendarPickerContainer {
  display: flex;
  flex-direction: column;
  /*align-items: center;*/
}

.dateRangeLabel {
  margin-right: 5px;
}

.taxMessage {
  padding: 5px;
  display: inline-block;
}
@media only screen and (min-width: 600px) {
  :host {
    flex-direction: row;
  }
  .calendarPickerContainer {
    flex-direction: row;
    align-items: center;
  }
}